.experience{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-bottom: 100px;

}
.experienceBody{

  box-shadow: 10px 9px 30px -1px rgba(0, 0, 0, 0.29);
   border-radius: 10px;
  overflow: hidden;


}
@media (max-width: 720px) {
  .experience{

    background-color: rgb(247, 247, 247);
  }
  .experienceBody{
    border-radius: 0px;

    box-shadow: none;
  }

}